<template>
  <div class="hc-hot-region hc-clearfix">
    <div @click="selectHotRegion(item)" class="hc-col-3 hc-one-in" v-for="(item, index) in hotRegionArr" :key="index">{{item.nodeName}}</div>
  </div>
</template>
<script>
export default {
  props: {
    hotRegion: Array,
    hotIndex: Number
  },
  data(){
    return {
      hotCityName:'',
    }
  },
  computed: {
    hotRegionArr(){
      if(!!this.hotRegion[this.hotIndex]){
        this.hotCityName = this.hotRegion[this.hotIndex].nodeName
        return this.hotRegion[this.hotIndex].children || [];
      }
    }
  },
  methods:{
    selectHotRegion(item){
      item.parentNode = this.hotCityName;
      this.$emit('selectHotRegion',item);
    }
  }
}
</script>
<style lang='scss' scoped>
.hc-hot-region {
  width: 100%;
  clear: both;
  padding: 4px 20px;
  text-align: center;
  color: #f3ab03;
  font-size: 14px;
  .hc-col-3 {
    padding: 0px 4px;
    line-height: 40px;
    height: 40px;
    display: -webkit-box !important;
  }
}
</style>




