<template>
  <div class="hc-f14 hc-c333" >
    <mt-popup position="right" v-model="$store.state.common.popObj.searchPop" class="mint-popup" :modal="true">
        <div class="hc-city-pop">
            <div class="hc-current-city" @click="closeCityChoose">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-lujing7"></use>
                </svg>
                当前城市：{{$store.state.common.location.allName}}
            </div>
            <div>
                <mt-index-list ref='fuck'>
                    <div class="hc-hot-title hc-bgf1">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-zu"></use>
                        </svg>
                        <span>热门城市</span>
                    </div>
                    <div class="hc-hot-city hc-clearfix">
                        <div v-for="(hotCityItem, index) in hotCityArr" :key="index">
                            <div @click="selectHotCity(index, regionHotIndex)" class="hc-col-4" v-for="(regionItem, regionHotIndex) in hotCityItem" :index='index' :key="regionHotIndex">
                                <div class="hc-city hc-one-in hc-one-in-1" :class="index==regionIndex&&regionHotIndex==hotCityCurrentIndex?'current':''">
                                    {{regionItem.nodeName}}
                                </div>
                            </div>
                            <HotRegionChoose @selectHotRegion="selectRegion" v-show="index==regionIndex" :hotRegion="hotCityItem" :hotIndex="hotCityCurrentIndex"/>
                        </div>
                    </div>
                    <mt-index-section v-for="(letter, index) in citySortArr" :key="index" :index="letter">
                        <mt-cell :ref="cityName.code" @click.native="selectCity(cityName.code)" v-for="(cityName, index1) in cityArr" :nodeCode="2" :key="index1" v-if="cityName.key == letter" :title="cityName.value">
                           <RegionChoose @selectRegion="selectRegion" v-show="cityName.code==selectCode" :region="cityName.regionArr"/>
                        </mt-cell>
                    </mt-index-section>
                </mt-index-list>
            </div>
        </div>
    </mt-popup>
  </div>
</template>
<script >
import { IndexList, IndexSection } from 'mint-ui'
import { makePy } from "../../../utils/pinying"//导入插件获取所有城市中文的大写首字母
import { queryAll, getHotCity } from '../../../api/axios'
import RegionChoose from './regionChoose'
import HotRegionChoose from './hotRegionChoose'

export default {
  name:"SearchPop",
  data() {
    return {
      arr: [],//存放初始筛选的城市名称
      cityArr: [],//存放第二次筛选后所有城市名称
      citySort: [],//存放初始获取城市首字母大写的数组
      citySortArr: [],//存放第二次筛选重复，不存在的城市首字母数组
      selectCode: 0,
      timer: null,
      hotCityArr: [],
      regionIndex: -1,
      hotCityCurrentIndex: 0,
    };
  },
  components: {
    IndexList,
    IndexSection,
    RegionChoose,
    HotRegionChoose
  },
  methods: {
    // 获取区域列表
    getQueryAll(){
      queryAll().then((data) => {
        // 提取出来所有的地址名称
        // console.log(data,'222');
        let arr = [];
        data.result.forEach(val => {
          let tempObj = {};
          tempObj.nodeName = val.nodeName;
          tempObj.nodeCode = val.nodeCode;
          tempObj.regionArr = val.children || [];
          this.arr.push(tempObj);
        });
        this.arr.forEach((val,i) => {
          let cityKey = makePy(val.nodeName)[0].substring(0,1);
          let cityValue = val.nodeName;
          let cityCode = val.nodeCode;
          let regionArr = val.regionArr;
          regionArr.forEach((regionVal, regionIndex) => {
            let subIndex = cityValue.indexOf('市') == -1 ? cityValue.length : cityValue.indexOf('市');
            regionVal.parentNode = cityValue.substring(0, subIndex)
          })
          this.citySort[cityKey] = cityKey;
          this.cityArr[i] = {
            key: cityKey,
            value: cityValue,
            code: cityCode,
            regionArr: regionArr
          };
        });
        for (let p in this.citySort) {
          this.citySortArr.push(this.citySort[p]);
        }
        this.citySortArr = this.citySortArr.sort();
      })
    },
    // 选择城市
    selectCity(code){
      this.selectCode = code;
      this.timer = setInterval(() => {
        let value = (document.getElementsByClassName('mint-indexlist-content')[0].scrollTop);
        var speed = (this.$refs[code][0].$el.offsetTop - value) / 8;
        document.getElementsByClassName('mint-indexlist-content')[0].scrollTop = value + speed;
        if (speed < 2 || value == document.getElementsByClassName('mint-indexlist-content')[0].scrollTop) {
            clearInterval(this.timer);
            document.getElementsByClassName('mint-indexlist-content')[0].scrollTop = this.$refs[code][0].$el.offsetTop;
        }
      }, 17)
    },
    // 选择区域
    selectRegion(data){
      this.$store.commit('LOCATION',{
        nodeName: data.nodeName,
        nodeCode: data.nodeCode,
        allName: data.parentNode + data.nodeName
      })
      this.closeCityChoose()
    },
    // 获取热门城市
    getHotCity(){
      getHotCity().then((result) => {
        // 每三个为一组
        let tempArr = [];
        let tempRegionArr = [];
        result.result.forEach((val, index) => {
          // let subIndex = val.name.indexOf('市') == -1 ? val.name.length : val.name.indexOf('市');
          // val.parentNode = val.name.substring(0, subIndex);
          tempRegionArr.push(val);
          if((index + 1)%3 == 0){
            tempArr.push(tempRegionArr);
            tempRegionArr = [];
          }else if((index+1) == result.result.length){
            tempArr.push(tempRegionArr);
          }
        })
        this.hotCityArr = tempArr;
        console.log(this.hotCityArr,'3443')

      })
    },
    // 选择热门城市
    selectHotCity(index, currentIndex){
      if(index == this.regionIndex && currentIndex == this.hotCityCurrentIndex){
        this.regionIndex = -1;
        return;
      }
      this.regionIndex = index;
      this.hotCityCurrentIndex = currentIndex;
    },
    // 关闭这个页面
    closeCityChoose(){
      this.$store.state.common.popObj.searchPop = false;
    }

  },
  updated() {
  },
  created () {
  },
  mounted() {
    this.getQueryAll();
    this.getHotCity();
  }
};
</script>
<style lang='scss'>
$padding15: 0 15px;
.hc-city-pop {
  .hc-current-city {
    height: 48px;
    padding: $padding15;
    line-height: 48px;
    top: 0px;
    left: 0px;
  }
  .hc-hot-title {
    padding: $padding15;
    height: 36px;
    line-height: 40px;
  }
  ul.mint-indexlist-content {
    margin-right: 0px !important;
    height: calc(100vh - 48px) !important;
  }
  .mint-indexlist-nav {
    position: fixed;
    margin-top: 100px;
    margin-bottom: 20px;
    background-color: rgba(0,0,0,0.2);
    border-radius: 100px;
    color: #fff;
    margin-right: 10px;
    width: 20px;
    .mint-indexlist-navitem {
      font-size: 14px;
      padding: 2px 0px;
  }
  }
  .hc-hot-city {
    padding: 10px;
    .hc-col-4 {
      padding: 6px 10px;
      .hc-city {
        text-align: center;
        height: 42px;
        line-height: 42px;;
        border: 1px solid #e5e5e5;
        &.current {
          background-color: #f3ab03;
          color: #ffffff;
          border-color: #f3ab03;
        }
      }
    }
  }
  .mint-indexsection-index {
      background-color: #f1f1f1;
      padding: 10px 15px;
  }
  .mint-cell-wrapper {
      background-image: none;
      padding-left: 0px;
      display: block;
      .mint-cell-text {
          line-height: 48px;
      }
  }
  .mint-indexsection {
    ul {
      .mint-cell {
        margin: 0px 15px;
      }
      .mint-cell::after {
        content: '';
        display: inline-block;
        width: 100%;
        position: absolute;
        height: 1px;
        background-color: #dbdbdb;
        bottom: 0px;
      }
      .mint-cell:last-child {
        background-image: none;
      }
      .mint-cell:last-of-type::after {
        height: 0px;
      }
    }
  }

}
</style>



