<template>
  <div class="hc-region-content">
    <div class="hc-col-4" v-for="(item, index) in region" :key="index">
      <div class="hc-region-item hc-one-in" @click="selectCity(item)">
          {{item.nodeName}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:{
    region: Array
  },
  data(){
     return {

     }
  },
  methods:{
    selectCity(item){
      console.log(this.region);
      console.log(item);
      console.log(9090)
      this.$emit('selectRegion',item)
    }
  }
}
</script>
<style lang="scss" scoped>
.hc-region-content {
  width: 100%;
  padding: 10px 0px;
  text-align: center;
  color: #353535;
  font-size: 14px;
  .hc-col-4 {
    padding: 6px;
  }
  .hc-region-item {
    height: 42px;
    line-height: 42px;
    border: 1px solid #e5e5e5;
  }
}
</style>



